@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html{
  height: 100%;
  width: 100%;
  margin: 0;
}
.ColorBox {
    width: 20%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    cursor: pointer;
    margin-bottom: -5px;
}

.ColorBox .copy-button, .back-button {
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255, 255, 255, 0.3);
    font-size: 1rem;
    line-height: 30px;
    text-transform: uppercase;
    border: none;
    transition: 0.4s;
    cursor: inherit;
    text-decoration: none;
}

.back-button {
    color: white;
}

.copy-button {
    opacity: 0;
}

.ColorBox:hover .copy-button {
    opacity: 1;
}

.ColorBox .box-content {
    position: absolute;
    width: 100%;
    padding: 10px;
    left: 0;
    bottom: 0;
    color: black;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.8rem;
}

.ColorBox .see-more {
    position: absolute;
    background: rgba(255, 255, 255, 0.3);
    text-align: right;
    padding: 10px;
    right: 0;
    bottom: 0;
    border: none;
    font-size: 0.8rem;
    text-transform: uppercase;
}

.ColorBox .copy-overlay {
    opacity: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    transition: -webkit-transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out;
    transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    background: inherit;
}

.ColorBox .copy-overlay.show {
    opacity: 1;
    -webkit-transform: scale(50);
            transform: scale(50);
    z-index: 10;
    position: absolute;
}

.ColorBox .copy-message {
    position: fixed;
    opacity: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: center;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 4rem;
    -webkit-transform: scale(0);
            transform: scale(0);
    color: white;
    transition: opacity 0.5s ease-in-out 0.3s;
}

.ColorBox .copy-message.show {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    z-index: 15;
}

.ColorBox .copy-message h1 {
    font-weight: 400;
    text-shadow: 2px 3px black;
    background: rgba(255, 255, 255, 0.3);
    width: 100%;
    margin-bottom: 0;
    text-transform: uppercase;
    padding: 1rem;
}

.ColorBox .copy-message p {
    font-weight: 100;
    font-size: 2rem;
}

.ColorBox.go-back {
    background-color: black;
    height: 50%;
}
.Navbar {
    display: flex;
    align-items: center;
    height: 6vh;
}

.Navbar .logo {
    margin-right: 15px;
    padding: 0 13px;
    font-size: 22px;
    background-color: #eceff1;
    font-family: 'Roboto', sans-serif;
    height: 100%;
    display: flex;
    align-items: center;
}

.Navbar .logo a, .Navbar .logo a:visited {
    text-decoration: none;
    color: #333;
}

.Navbar .slider {
    width: 350px;
    margin: 0 10px;
    display: inline-block;
}

.Navbar .rc-slider-track {
    background-color: transparent;
}

.Navbar .rc-slider-rail {
    height: 8px;
}

.Navbar .rc-slider-handle, .Navbar .rc-slider-handle:active, .Navbar .rc-slider-handle:focus, .Navbar .rc-slider-handle:hover {
    background-color: green;
    outline: none;
    border: green;
    box-shadow: none;
    width: 15px;
    height: 15px;
    margin-top: -4px;
}

.Navbar .select-container {
    margin-left: auto;
    margin-right: 1rem;
}


.Palette {
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.Palette-colors {
    height: 90%;
}

.Palette-footer{
    background-color: white;
    height: 5vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
}
.Palette .emoji{
    margin: 1rem;
}
